@import url('https://fonts.googleapis.com/css2?family=Merriweather&family=Montserrat:wght@100;300;400;500;700;900&family=Poppins:wght@300;400&family=Roboto&family=Sacramento&family=Ubuntu&display=swap');

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.Montserrat{
  font-family: Montserrat;
}
.Montserrat500{
  font-family: Montserrat;
  font-weight: 500;
}
.Montserrat700{
  font-family: Montserrat;
  font-weight: 700;
}
.Roboto400{
  font-family: Roboto;
  font-weight: 400;
}
.Bold{
  font-weight: bold;
}
.Ubuntu{
  font-family: Ubuntu;
}
/* back-ground styling */
body{
  background-color: #F1F1F1;
}
.FrontImage{
  width: 100%;
  height: 630px;
  filter: grayscale(10%);
  background-image: url("../public/images/homeMain.jpg");
}
/*navbar styling*/
.navbar{
  font-family: Montserrat;
  line-height: 1.5;
  box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.3px solid gray;
}
.navbar-brand{
  font-family: Montserrat;
}
.navbarLogo{
  position: relative;
  bottom: 1.5px;
}
.nav-item{
  text-transform: uppercase;
}

/** Footer Styling **/
.link{
  text-decoration: none;
  font-size: 1.1rem;
  color:grey;
}
.link:hover{
  color: white;
}

/** page not found **/

/*About us*/
.aboutHeading{
  font-family: Montserrat;
  font-weight: bold;
}
.aboutText{
  font-size: 1.2rem;
  font-family: ubuntu;
}
.aboutSpace{
  margin-top:80px;
}
/* Privacy Policy */
#privacyTerms .card{
  flex-direction: row;
  max-width:75%;
}
#privacyTerms .card img{
  max-width: 23%;
}
/* ===========================
=======Register============ */
.register{
  margin-top: 100px;
  border-radius: 25px;
  width: 60rem;
}

/*=================
========Products=========*/
.productLink{
  text-decoration: none !important;
  color: black;
  font-family: ubuntu;
}